import React from "react";
import cn from "classnames";

export default function Stamp({ stampOptions, setStampOptions }) {
	// handle file upload
	const handleFileChange = e => {
		if (e.target.files[0]) {
			const reader = new FileReader();
			reader.readAsDataURL(e.target.files[0]);
			reader.onload = img => {
				const src = img.target.result;

				// prevent duplication
				if (stampOptions.images.includes(src)) {
					alert("image uploaded!");
				}

				if (stampOptions.images.length === 2) {
					setStampOptions({
						...stampOptions,
						images: [src, stampOptions.images[0]],
						currentImage: src
					});
				} else {
					if (!stampOptions.images.includes(src)) {
						setStampOptions({
							...stampOptions,
							images: [src, ...stampOptions.images],
							currentImage: src
						});
					}
				}
			};
		}
	};

	return (
		<div>
			<h1>Stamp</h1>
			<section>
				<label>
					Max Width: {stampOptions.maxWidth}
					<input
						type="range"
						min="1"
						max="800"
						value={stampOptions.maxWidth}
						onChange={e =>
							setStampOptions({ ...stampOptions, maxWidth: e.target.value })
						}
						name="max-width"
					/>
				</label>
			</section>
			<section>
				<label>
					Upload an Image
					<input
						type="file"
						className="stamp-file-upload"
						accept="image/*"
						onChange={handleFileChange}
					/>
				</label>
			</section>
			{stampOptions.images && (
				<section>
					{stampOptions.images.map((image, index) => {
						return (
							<img
								className={cn(
									"stamp-image",
									!(image === stampOptions.currentImage) && "stamp-image-blur"
								)}
								src={image}
								alt="I love gakki"
								key={index}
								onClick={e => {
									setStampOptions({
										...stampOptions,
										currentImage: image
									});
								}}
							/>
						);
					})}
				</section>
			)}
		</div>
	);
}



