import React from "react";

export default function Save() {
	return (
		<div>
			<h1>Download</h1>
			<section>
				<a
					className="button download"
					download="myCanvas.png"
					href={document.getElementById("canvas").toDataURL()}
				>
					Download Image
				</a>
			</section>
		</div>
	);
}



