import React from "react";

import Pen from "./panels/pen";
import Eraser from "./panels/eraser";
import Stamp from "./panels/stamp";
import Save from "./panels/save";

export default function ToolPanel({
	canvas,
	activeTool,
	penOptions,
	setPenOptions,
	eraserOptions,
	setEraserOptions,
	stampOptions,
	setStampOptions,
	saveOptions,
	setSaveOptions
}) {
	return (
		<div className="tool-panel">
			{activeTool === "pen" && (
				<Pen penOptions={penOptions} setPenOptions={setPenOptions} />
			)}
			{activeTool === "eraser" && (
				<Eraser
					eraserOptions={eraserOptions}
					setEraserOptions={setEraserOptions}
				/>
			)}
			{activeTool === "stamp" && (
				<Stamp stampOptions={stampOptions} setStampOptions={setStampOptions} />
			)}
			{activeTool === "save" && (
				<Save
					saveOptions={saveOptions}
					setSaveOptions={setSaveOptions}
					canvas={canvas}
				/>
			)}
		</div>
	);
}



