import React from "react";

export default function Eraser({ eraserOptions, setEraserOptions }) {
	const handleClearCanvas = e => {
		const canvas = document.getElementById("canvas");
		const ctx = canvas.getContext("2d");
		ctx.fillStyle = "#fff";
		ctx.fillRect(0, 0, canvas.width, canvas.height);
	};
	return (
		<div>
			<h1>Eraser</h1>
			<section>
				<label>
					Stroke Width: {eraserOptions.strokeWidth}
					<input
						type="range"
						min="1"
						max="100"
						value={eraserOptions.strokeWidth}
						onChange={e =>
							setEraserOptions({
								...eraserOptions,
								strokeWidth: e.target.value
							})
						}
						name="stroke-width"
					/>
				</label>
			</section>
			<section>
				<button className="button" onClick={handleClearCanvas}>
					Clear Canvas
				</button>
			</section>
		</div>
	);
}



