import React from "react";
import cn from "classnames";
import { removeRepeatedColor } from "../../util/util";

export default function Pen({ penOptions, setPenOptions }) {
	const handleColorChange = e => {
		const color = e.target.value;
		let colorArray = removeRepeatedColor(penOptions.strokeColorHistory, color);
		if (colorArray.length === 6) {
			colorArray = [color, ...colorArray.slice(0, 5)];
		} else {
			colorArray = [color, ...colorArray];
		}
		setPenOptions({
			...penOptions,
			strokeColor: color,
			strokeColorHistory: colorArray
		});
	};

	return (
		<div className="panels-pen">
			<h1>Pen</h1>
			<section>
				<label>
					Stroke Color:
					<input
						type="color"
						value={penOptions.strokeColor}
						onChange={handleColorChange}
					/>
				</label>
				<div className="panels-pen-colors">
					{penOptions.strokeColorHistory.map((color, index) => (
						<div
							className={cn(
								color === penOptions.strokeColor && "color-selected"
							)}
							style={{ backgroundColor: color }}
							key={index}
							onClick={e => {
								setPenOptions({
									...penOptions,
									strokeColor: color
								});
							}}
						/>
					))}
				</div>
			</section>
			<section>
				<label>
					Stroke Width: {penOptions.strokeWidth}
					<input
						type="range"
						min="1"
						max="100"
						value={penOptions.strokeWidth}
						onChange={e =>
							setPenOptions({ ...penOptions, strokeWidth: e.target.value })
						}
						name="stroke-width"
					/>
				</label>
			</section>
			<section>
				<label>Line Type:</label>
				<div className="line-types">
					<label
						className={
							penOptions.lineType === "solid"
								? "type-selected"
								: "type-unselected"
						}
					>
						<input
							type="radio"
							value="solid"
							checked={penOptions.lineType === "solid"}
							onChange={e =>
								setPenOptions({ ...penOptions, lineType: e.target.value })
							}
						/>
						<span className="line-types__option">Solid</span>
					</label>
					<label
						className={
							penOptions.lineType === "dash"
								? "type-selected"
								: "type-unselected"
						}
					>
						<input
							type="radio"
							value="dash"
							checked={penOptions.lineType === "dash"}
							onChange={e =>
								setPenOptions({ ...penOptions, lineType: e.target.value })
							}
						/>
						<span className="line-types__option">Dashed</span>
					</label>
					<label
						className={
							penOptions.lineType === "dot"
								? "type-selected"
								: "type-unselected"
						}
					>
						<input
							type="radio"
							value="dot"
							checked={penOptions.lineType === "dot"}
							onChange={e =>
								setPenOptions({ ...penOptions, lineType: e.target.value })
							}
						/>
						<span className="line-types__option">Dotted</span>
					</label>
				</div>
			</section>
		</div>
	);
}



