import React, { useRef, useState, useEffect } from "react";
import { getLineType, getImageDrawInfo } from "../util/util";

export default function Canvas({
	activeTool,
	penOptions,
	eraserOptions,
	stampOptions,
	saveOptions
}) {
	const canvasRef = useRef(null);
	const [isDrawing, setIsDrawing] = useState(false);
	const [isErasing, setIsErasing] = useState(false);
	const [ctx, setCtx] = useState(null);

	// componentDidMount
	useEffect(() => {
		const canvas = canvasRef.current;
		setCtx(canvas.getContext("2d"));

		// set canvas background
		const ctx = canvas.getContext("2d");
		ctx.fillStyle = "#fff";
		ctx.fillRect(0, 0, canvas.width, canvas.height);
	}, []);

	// // clear the canvas
	// useEffect(
	// 	() => {
	// 		if (eraserOptions.clearCanvas) {
	// 			const canvas = canvasRef.current;
	// 			ctx.fillStyle = "#fff";
	// 			ctx.fillRect(0, 0, canvas.width, canvas.height);
	// 		}
	// 	},
	// 	[eraserOptions.clearCanvas]
	// );

	const getX = event => {
		if (event.pageX === undefined) {
			return event.targetTouches[0].pageX - canvasRef.current.offsetLeft;
		} else {
			return event.pageX - canvasRef.current.offsetLeft;
		}
	};

	const getY = event => {
		if (event.pageY === undefined) {
			return event.targetTouches[0].pageY - canvasRef.current.offsetTop;
		} else {
			return event.pageY - canvasRef.current.offsetTop;
		}
	};

	const start = event => {
		// prevent the right mouse down
		if (event.button === 2) return;

		if (activeTool === "pen") {
			setIsDrawing(true);
			ctx.beginPath();
			ctx.moveTo(getX(event), getY(event));
		} else if (activeTool === "eraser") {
			setIsErasing(true);
			ctx.beginPath();
			ctx.moveTo(getX(event), getY(event));
		} else if (activeTool === "stamp") {
			const img = new Image();
			img.src = stampOptions.currentImage;
			ctx.drawImage(
				img,
				...getImageDrawInfo(
					img.width,
					img.height,
					getX(event),
					getY(event),
					stampOptions.maxWidth
				)
			);
		}
		event.preventDefault();
	};

	const draw = event => {
		if (isDrawing) {
			ctx.lineTo(getX(event), getY(event));
			ctx.setLineDash(getLineType(penOptions.lineType, penOptions.strokeWidth));
			ctx.lineWidth = penOptions.strokeWidth;
			ctx.lineJoin = "round";
			ctx.strokeStyle = penOptions.strokeColor;
			ctx.stroke();
		} else if (isErasing) {
			ctx.lineTo(getX(event), getY(event));
			ctx.lineWidth = eraserOptions.strokeWidth;
			ctx.lineJoin = "round";
			ctx.strokeStyle = eraserOptions.strokeColor;
			ctx.stroke();
		}
		event.preventDefault();
	};

	const end = event => {
		if (isDrawing) {
			ctx.stroke();
			ctx.closePath();
			setIsDrawing(false);
		} else if (isErasing) {
			ctx.stroke();
			ctx.closePath();
			setIsErasing(false);
		}
		event.preventDefault();
	};

	return (
		<canvas
			id="canvas"
			width={window.innerWidth - 262}
			height={window.innerHeight}
			className="canvas"
			ref={canvasRef}
			onMouseDown={start}
			onMouseMove={draw}
			onMouseUp={end}
		/>
	);
}



