import React, { useState, useEffect } from "react";

import "./app.css";

import NavBar from "./nav-bar";
import ToolPanel from "./tool-panel";
import Canvas from "./canvas";

export default function App() {
	/*
		If the syntax below (`useState(...)` etc.) looks unfamiliar to you,
		check out the docs on "React Hooks" we link to in the Resources section of the README.
	*/
	const [activeTool, setActiveTool] = useState(
		localStorage.getItem("activeTool") || "pen"
	);
	const [penOptions, setPenOptions] = useState(
		JSON.parse(localStorage.getItem("penOptions")) || {
			strokeWidth: 10,
			lineType: "solid",
			strokeColor: "#000000",
			strokeColorHistory: []
		}
	);
	const [eraserOptions, setEraserOptions] = useState(
		JSON.parse(localStorage.getItem("eraserOptions")) || {
			strokeWidth: 10,
			strokeColor: "#fff",
			clearCanvas: 0
		}
	);
	const [stampOptions, setStampOptions] = useState(
		JSON.parse(localStorage.getItem("stampOptions")) || {
			maxWidth: 200,
			images: [],
			currentImage: null
		}
	);

	// localStorage
	useEffect(
		() => {
			if (activeTool === "save") {
				localStorage.setItem("activeTool", "pen");
			} else {
				localStorage.setItem("activeTool", activeTool);
			}
		},
		[activeTool]
	);

	useEffect(
		() => {
			localStorage.setItem("penOptions", JSON.stringify(penOptions));
		},
		[penOptions]
	);

	useEffect(
		() => {
			localStorage.setItem("eraserOptions", JSON.stringify(eraserOptions));
		},
		[eraserOptions]
	);

	useEffect(
		() => {
			localStorage.setItem(
				"stampOptions",
				JSON.stringify({
					...stampOptions,
					images: [],
					currentImage: null
				})
			);
		},
		[stampOptions]
	);

	return (
		<React.Fragment>
			<NavBar activeTool={activeTool} setActiveTool={setActiveTool} />
			<ToolPanel
				activeTool={activeTool}
				penOptions={penOptions}
				setPenOptions={setPenOptions}
				eraserOptions={eraserOptions}
				setEraserOptions={setEraserOptions}
				stampOptions={stampOptions}
				setStampOptions={setStampOptions}
			/>
			<Canvas
				activeTool={activeTool}
				penOptions={penOptions}
				eraserOptions={eraserOptions}
				stampOptions={stampOptions}
			/>
		</React.Fragment>
	);
}



